import React, {Component} from 'react';
import ReactDOM from 'react-dom';

// three.js imports
import * as THREE from "three";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { VertexNormalsHelper } from 'three/examples/jsm/helpers/VertexNormalsHelper';
import { VertexTangentsHelper } from 'three/examples//jsm/helpers/VertexTangentsHelper';
import { BufferGeometryUtils } from 'three/examples//jsm/utils/BufferGeometryUtils';

// assets
import LeePerrySmith from './models/LeePerrySmith.glb'
import skull from './models/LeePerryWarp.glb'
import bgm from './audio/Podington_Bear_Humble_Pie.mp3';
import god from './audio/Metek__Zreen_Toyz_-_05_-_Rising_Terror_edit.mp3';
import henDead from './img/Hen-dead-3x.png'
import './index.css';


// import Gallery from "react-photo-gallery";
// import Carousel, { Modal, ModalGateway } from "react-images";

import './style/style.css';


import { motion } from "framer-motion"

import * as W69 from './Windows69.jsx';
// import * as Content from './content.jsx';
import * as Pet from './Pet.jsx'



class Desktop extends Component{
  // ...
    constructor(props){
      super(props);
      this.state = {henDeath:false}
      this.henDeath = this.henDeath.bind(this);
    }

  henDeath(){
    this.props.stopMusic("bgm");
    this.setState({henDeath:true});
  }


    render(){
      return(
        <div>
        <W69.Icon
          image={require('./img/bonzi.png')}
          name="pet.exe"
          id="petIcon"
          className="desktopApp"
          playMusic={this.props.playMusic}
          stopMusic={this.props.stopMusic}
          windowId="PetWindow" 
          content={<Pet.Pet
                    addTotals={this.props.addTotals}
                    henDeath={this.henDeath}
                    godMode={this.props.godMode}/>}
          title="virtual pet"
          />
          <div className={this.state.henDeath? "gameOver" : "invisibile"}>
            <b>Game Over</b><p/>
            Hen has died<p/>
            <img src={henDead}/><p/>
            <hr/>
            refresh to try again
            </div>
          </div>
        )

    }
}



class App extends Component{

  constructor(props){
    super(props);
    this.state={godMode:false,
                totalFeed:0,
                totalPet:0,
                totalTalk:0}
    
    this.bgmAudio = new Audio(bgm);
    this.bgmAudio.loop=true;
    this.godAudio = new Audio(god);
    this.godAudio.loop=true;
    
    this.playMusic = this.playMusic.bind(this);
    this.stopMusic = this.stopMusic.bind(this);
    this.godMode = this.godMode.bind(this);
    this.getPreTotals = this.getPreTotals.bind(this);

    this.preTotals={totalFeed:0,totalPet:0,totalTalk:0};
  }

  getPreTotals(d){
    this.preTotals = d;
  }

  componentDidMount() {
  var scene, renderer;
  var camera, light;
  var vnh;
  var vth;


  

        renderer = new THREE.WebGLRenderer();
        renderer.setPixelRatio( window.devicePixelRatio );
        renderer.setSize( window.innerWidth, window.innerHeight );
        // document.body.appendChild( renderer.domElement );
        this.mount.appendChild(renderer.domElement);

        //

        camera = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 1, 1000 );
        camera.position.z = 400;

        scene = new THREE.Scene();

        light = new THREE.PointLight();
        light.position.set( 200, 100, 150 );
        scene.add( light );

        scene.add( new THREE.PointLightHelper( light, 15 ) );

        var gridHelper = new THREE.GridHelper( 400, 40, 0x0000ff, 0x808080 );
        gridHelper.position.y = - 150;
        gridHelper.position.x = - 150;
        scene.add( gridHelper );

        var polarGridHelper = new THREE.PolarGridHelper( 200, 16, 8, 64, 0x0000ff, 0x808080 );
        polarGridHelper.position.y = - 150;
        polarGridHelper.position.x = 200;
        scene.add( polarGridHelper );

        const loader = new GLTFLoader();
        loader.load( skull, function ( gltf ) {

          var mesh = gltf.scene.children[ 0 ];

          BufferGeometryUtils.computeTangents( mesh.geometry ); // generates bad data due to degenerate UVs

          var group = new THREE.Group();
          group.scale.multiplyScalar( 50 );
          scene.add( group );

          // To make sure that the matrixWorld is up to date for the boxhelpers
          group.updateMatrixWorld( true );

          group.add( mesh );

          vnh = new VertexNormalsHelper( mesh, 5 );
          scene.add( vnh );

          vth = new VertexTangentsHelper( mesh, 5 );
          scene.add( vth );

          scene.add( new THREE.BoxHelper( mesh ) );

          var wireframe = new THREE.WireframeGeometry( mesh.geometry );
          var line = new THREE.LineSegments( wireframe );
          line.material.depthTest = false;
          line.material.opacity = 0.25;
          line.material.transparent = true;
          line.position.x = 4;
          group.add( line );
          scene.add( new THREE.BoxHelper( line ) );

          var edges = new THREE.EdgesGeometry( mesh.geometry );
          var line = new THREE.LineSegments( edges );
          line.material.depthTest = false;
          line.material.opacity = 0.25;
          line.material.transparent = true;
          line.position.x = - 4;
          group.add( line );
          scene.add( new THREE.BoxHelper( line ) );

          scene.add( new THREE.BoxHelper( group ) );
          scene.add( new THREE.BoxHelper( scene ) );


        } );

        //

        window.addEventListener( 'resize', onWindowResize, false );


      function onWindowResize() {

        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();

        renderer.setSize( window.innerWidth, window.innerHeight );

      }

      function animate() {

        requestAnimationFrame( animate );

        var time = - performance.now() * 0.0003;

        camera.position.x = 200 * Math.sin( time/2 );
        camera.position.z = 100 + (309 * Math.cos( time/4 ));
        camera.position.y = (100 * Math.sin( time )) - 25;

        camera.lookAt( scene.position );

        light.position.x = Math.sin( time * 1.7 ) * 300;
        light.position.y = Math.cos( time * 1.5 ) * 400;
        light.position.z = Math.cos( time * 1.3 ) * 300;

        if ( vnh ) vnh.update();
        if ( vth ) vth.update();

        renderer.render( scene, camera );

      }

    animate(); 
    }

    stopMusic(song){
      switch(song){
        case "bgm":
            this.bgmAudio.pause()
          break;
        case "god":
            this.godAudio.pause()
          break;
        default:
          console.log("not a valid song to stop")
      }
    }

    playMusic(song){
      switch(song){
        case "bgm":
          if(this.bgmAudio.paused){
            this.bgmAudio.play()
          }
          break;
        case "god":
          if(this.godAudio.paused){
            this.godAudio.play()
          }
        default:
          console.log("not a valid song to play")
      }

    }

  godMode(){
    this.setState({godMode:true});
    this.stopMusic("bgm");
    this.playMusic("god");
  }

  render() {
    // const {isVisible} = this.state;
    // display:none to invisible. Block to visible.
    return (
        <div className="content" id="main">
        <Desktop
          playMusic={this.playMusic}
          stopMusic={this.stopMusic}
          godMode={this.godMode}
          addTotals={this.getPreTotals}/>
        <div id="threedhell" ref={ref => (this.mount = ref)} className={this.state.godMode? "": "invisibile"}/> 
          <div id="overlayText" className={this.state.godMode? "": "invisibile"}>
            <W69.GodFinal
              stopMusic={this.stopMusic}
              preTotals={this.preTotals}
              />
          </div>
        </div>
      )
  }
}

ReactDOM.render(<App/>, document.getElementById('root'));