import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import speechBlip from './audio/185828__lloydevans09__little-thing.wav'
import * as face from './henFaces.jsx';
import {story} from './MasterStory.jsx';


class WorldState{
	constructor(x, convo_cond, food_cond, love_cond){
		this.x = x
	}
	sayOwie(){
		alert("owie"+this.x)
	}
}




class Pet extends Component{

	constructor(props){
	    super(props)
	    this.state = {hunger:5,
	    			  love:5,
					  text:"> Heya My name's Hen, the human pet! ",
					  conversations:0,
	    			  speaking:false,
	    			  emotion:"basic",
	    			  name:"unknown",
	    			  faith:0,
	    			  feedCount:0,
	    			  talkCount:0
	    			  }
	    this.feed.bind(this);
	    this.typeWriter.bind(this);
	    this.boop = new Audio(speechBlip);
	    console.log(face);
	  }



	feed(){
		if(!this.state.speaking){
		  	this.setState({speaking:true})
		  	if(this.state.hunger==4){
		  		this.setState({faith:this.state.faith+1});
		  	}
		  	if(this.state.hunger==9){
		  		console.log("hen has died x_x");
		  		this.props.henDeath();
		  		// call death screen
		  	}
		  	else{
			  	this.setState({text:"", 
			  				   emotion:story.feed[this.state.hunger].emotion
			  				},
			  				e=>this.typeWriter(story.feed[this.state.hunger].text,0));
			  	}
		  	}
	}
	
	pet(){
		if(!this.state.speaking){
		  	this.setState({speaking:true})
		  	if(this.state.love==14 || this.state.love==18){
		  			this.setState({faith:this.state.faith+1})
		  	}
		  	if(this.state.love>=19){
		  		this.setState({text:"", 
		  				  	   emotion:story.pet[19].emotion
		  					   },
		  					  e=>this.typeWriter(story.pet[19].text,0));
			  	}
		  	else{
			  	this.setState({text:"", 
			  				   emotion:story.pet[this.state.love].emotion
			  				},
			  				e=>this.typeWriter(story.pet[this.state.love].text,0));
				  	}

		  	}
	}

	talk(){

		if(!this.state.speaking){
			this.setState({speaking:true})
			if(this.state.faith>=2){
				console.log("got high faith")
				this.setState({faith:0,conversations:100},
					this.setState({text:"", 
				  			   emotion:story.talk[100].emotion
				  			},
				  			e=>this.typeWriter(story.talk[100].text,0))
				)
				return;
			}
			switch(this.state.conversations){
 				case 13:
 					this.setState({hunger:2});
 					break;
 				case 20:
 					this.setState({text:"", 
			  			   emotion:story.talk[19].emotion
			  			},
			  			e=>this.typeWriter(story.talk[19].text,0))
 					this.setState({conversations:19})
 					return;
 					break;
 				case 107:
 					this.props.godMode();
 					this.props.addTotals({totalFeed:this.state.feedCount, totalPet:this.state.love-5, totalTalk:this.state.talkCount})
 				default:
 					break;
			}
			if(this.state.conversations<107){
				console.log("here with state: ",this.state.conversations)
				this.setState({text:"", 
				  			   emotion:story.talk[this.state.conversations].emotion
				  			},
				  			e=>this.typeWriter(story.talk[this.state.conversations].text,0));
				}

			}
				
	}

	typeWriter(txt,i,speed=50,callback=()=>{}) {
	  if(i==txt.length){
	  	this.setState({speaking:false})
	  	callback()
	  	return
	  }
	  else{
	  	  let ma =  new Audio(speechBlip);
	  	  ma.play();
		  this.setState({text:this.state.text+txt.charAt(i)})
		  setTimeout(()=>this.typeWriter(txt,i+1,speed),speed);

	  }

	}

	render(){
		return(
	<div className="">
		<div className="petText">{this.state.text}</div>
		<div className="petDisplay"><center>
			<img src={face.emotions[this.state.emotion]}/>
		</center></div>

		<div className="petControls">
			<div className="petUIbutton"
				onClick={!this.state.speaking ? ()=>this.setState({hunger:this.state.hunger+1,feedCount:this.state.feedCount+1}, e=>this.feed()) : e=>{}}>
				feed</div> 
			<div className="petUIbutton"
				onClick={!this.state.speaking ? ()=>this.setState({love:this.state.love+1}, e=>this.pet()) : e=>{}}>
				pet</div>
			<div className="petUIbutton"
						onClick={!this.state.speaking ? () => this.setState({ conversations: this.state.conversations + 1,talkCount:this.state.talkCount+1 }, e=>this.talk()) : e=>{console.log("no clicky")}}>
				talk</div>
		</div>
	</div>

			)

		}
}

export {Pet}