import { motion } from "framer-motion"
import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import GlitchClip from 'react-glitch-effect/core/Clip';
import lowVoice from './audio/lowvoice.wav'
import upVoice from './audio/upvoice.wav'


import {story} from './GodStory.jsx';


// variants ------
const popupAnim = {
  visible:{
           scaleX:1,
           scaleY:1,
           transition:{
            type: 'spring',
            damping:30,
            stiffness:1000  
           }},

  hidden:{
          scaleX:0,
          scaleY:0,
          ease:'anticipate',
          }
}


  const fullSlide = {
  visible:{
          y:0,
          transition:{ ease: "easeInOut"}

        },

  hidden:{
          y:window.screen.height,
          transition:{ ease: "easeInOut"}

          }
}


const slideDown = {
  visible:{
           height:null,
         },

  hidden:{
          height:0,
          }
}


class GenericWindow extends Component {
  constructor(props){
    super(props);
    this.state = {draggable:false, mouseUp:false, closeIcon:false, closed:false}
  }
    
  render(){

        if(this.props.content != null){
        return(       
        <motion.div 
            drag={this.state.draggable}
            dragMomentum={false}
            className={this.props.visibile ? "ContentWindow "+this.props.additionalClasses : "ContentWindow invisibile"}
            style={this.props.overrideStyle}
            id={this.props.id}
            animate={(this.props.show) ? "visible":"hidden"}
            // animate={(this.props.show && this.props.toggle) ? "visible":"hidden"}

            variants={window.screen.width/window.screen.height > 1 ? popupAnim : fullSlide}
            >
              <motion.div 
              className="contentTopBar homeBar"
              onMouseEnter={ e=>this.setState({draggable:true, mouseUp:false}) }  
              onMouseLeave={ e=>this.state.mouseUp && this.setState({draggable:false, mouseUp:false}) }
              // alternatively I can use:
              // onHoverStart
              // onHoverEnd

              onMouseDown={e=>this.setState({draggable:true,mouseUp:false})}
              onMouseUp={ e=>{this.setState({mouseUp:true})} }
              >
  
                {this.props.title}

                <span 
                  className="close" 
                  id={this.props.id+"_close"}
                  onClick={(e)=>{
                    console.log("called");
                    e.stopPropagation();
                    this.props.toggleFunction();
                    this.props.closedWindow();
                    if(this.props.onWindowClose){
                      this.props.onWindowClose();
                    }
                  }}
                >x</span>


              </motion.div>
                {this.props.content} 
                {this.props.children}
  
        </motion.div>
      )}
      else{
        return(null)
      }
      }

  }
  


class OpenWindowForever extends Component{
  constructor(props){
    super(props)
    this.state = {showWindow:false, clicked:false}
  }

  toggleWindow(){
    this.setState({showWindow:!this.state.showWindow});
  }
  
  render(){
    return(
      <div>
        <div 
          className="link"
          onClick={(e)=>{
            e.stopPropagation()
            this.toggleWindow(); 
            this.setState({clicked:true, showWindow:true});
            if(this.props.onSetWindow){
              this.props.onSetWindow(this.props.id)
            }
          }}>
          {this.props.children}
        </div>
            <GenericWindow 
              visibile={this.state.clicked}
              show={this.state.showWindow} 
              toggle={this.state.showWindow}
              additionalClasses={this.props.additionalClasses}
              toggleFunction={this.toggleWindow.bind(this)}
              id={this.props.id} 
              content={this.props.content} 
              title={this.props.title}
              overrideStyle={this.props.overrideStyle}
              onWindowClose={this.props.onWindowClose}
              closedWindow={this.props.closedWindow}
              />
      </div>
      )
  }
}


class OpenWindow extends Component{
  constructor(props){
    super(props)
    this.state = {showWindow:false, clicked:false}
  }

  toggleWindow(){
    this.setState({showWindow:!this.state.showWindow});

  }

  render(){
    return(
    <span>
      <span 
        className="menuItem"
        onClick={()=>{this.toggleWindow(); this.setState({clicked:true, showWindow:true})}}
        >
        {this.props.text}
      </span>

      <GenericWindow 
        visibile={this.state.clicked || this.props.forceShowWindow}
        show={(this.props.id === this.props.targetWindow) && (this.state.showWindow || this.props.forceShowWindow)} 
        toggle={this.state.showWindow}

        overrideStyle={this.props.overrideStyle}
        toggleFunction={this.toggleWindow.bind(this)}
        id={this.props.id} 
        content={this.props.content} 
        title={this.props.title}
        closedWindow={this.props.closedWindow}

      />
    </span>
    )
  }
}



class OpenGallery extends Component{
  constructor(props){
    super(props)
    this.state = {showWindow:false, clicked:false}
    this.parentToContent.bind(this)
  }

  toggleWindow(){
    this.setState({showWindow:!this.state.showWindow});
  }

  parentToContent(id){
    var photo = document.getElementById(id);
    // photo.id  = "newId";
    document.getElementById("main").appendChild(photo); 
  }

  render(){
    return(
    <span>
      <span 
        className="menuItem"
        onClick={()=>{this.toggleWindow(); this.setState({clicked:true, showWindow:true}); this.parentToContent(this.props.id)}}
        >
        {this.props.text}
      </span>


      <Gallery
        show={(this.props.id === this.props.targetWindow) && this.state.showWindow} 
        toggle={this.state.showWindow}

        overrideStyle={this.props.overrideStyle}
        toggleFunction={this.toggleWindow.bind(this)}
        id={this.props.id} 
        content={this.props.content} 
        title={this.props.title}
        closedWindow={this.props.closedWindow}

      />

    </span>
    )
  }
}

class Gallery extends Component{
 constructor(props){
    super(props);
    this.state = {mouseUp:false, closeIcon:false, closed:false}
  }
    
  render(){

        if(this.props.content != null){
        return(       
        <div 
            className={this.props.show ? "Gallery "+this.props.additionalClasses : "Gallery invisibile"}
            style={this.props.overrideStyle}
            id={this.props.id}
            >
              <div 
                className="galleryTopBar"
              >
                {this.props.title}
                <span 
                  className="close" 
                  id={this.props.id+"_close"}
                  onClick={(e)=>{
                    e.stopPropagation();
                    this.props.toggleFunction();
                    this.props.closedWindow();
                    if(this.props.onWindowClose){
                      this.props.onWindowClose();
                    }
                  }}
                >x</span>
              </div>
                {this.props.content} 
                {this.props.children}
  
        </div>
      )}
      else{
        return(null)
      }
      }

}

class DropDown extends Component{
  constructor(props){
    super(props)
    this.state = {clicked:false}
  }

  render(){
    if((this.props.target === this.props.id) && !this.state.clicked){
      this.setState({clicked:true})    
    }
    
    return(
      <motion.div
        className={this.state.clicked ? "submenu" : "submenu invisibile"}
        animate={(this.props.target === this.props.id) ? "visible":"hidden"}
        variants={slideDown}
      >
        {this.props.children}
      </motion.div>
      )
  }
}

class SimpleDropDown extends Component{
  constructor(props){
    super(props)
    this.state = {clicked:false}
  }

  render(){
    return(
      <div>
      <a
      onClick={e=>this.setState({clicked:!this.state.clicked})}
      >
        {this.props.linkText}
      </a>
      <motion.div
      className={this.state.clicked ? "" : "invisibile"}
      animate={this.state.clicked ? "visible" : "hidden"}
      variants={slideDown}
      style={{overflowY:'hidden'}}
      >
      {this.props.children}
      </motion.div>
      </div>
      )
  }
}


class Icon extends Component{
  constructor(props){
    super(props)
    this.doubleClick.bind(this);
    this.state = {doubleClick:false,
                  open:false, 
                  opened:false,
                  }

  }


  doubleClick(){
    if(this.state.doubleClick){
      console.log("YES!")
      this.props.playMusic("bgm")      
      // if(this.bgmAudio.paused){
      //   this.bgmAudio.play();

      // }
      this.setState({doubleClick:false, open:true, opened:true})
      
    }
    else{
      this.setState({doubleClick:true})
      setTimeout(()=>{this.setState({doubleClick:false})},1000);
    }
  }

  toggleWindow(){
    this.setState({open:!this.state.open});
  }
  render(){
    return(
      <div
      className={this.props.className}
      >
        <motion.div
          className="icon"
          drag={window.screen.height/window.screen.width > 1 ? false : true}
          dragMomentum={false}
          onClick={()=>{this.doubleClick()}}
          id={this.props.id}

        > 
            <img src={this.props.image}/><br/>
            <span>{this.props.name}</span>

        </motion.div>

        <GenericWindow 
                visibile={this.state.opened}
                show={this.state.open} 
                toggle={this.state.open}

                toggleFunction={this.toggleWindow.bind(this)}
                id={this.props.windowId} 
                content={this.props.content} 
                title={this.props.title}
                overrideStyle={this.props.overrideStyle}
                closedWindow={()=>{}}
        />

    
      </div>
      )

  }
}


class GodFinal extends Component{

  constructor(props){
      super(props)
      this.state = {text:"HEN HAS AWAKENED",
                    speaking:false,
                    playerTurn:true,
                    feedCount:0,
                    petCount:0,
                    talkCount:0,
                    henCount:0,
                    gameOver:false,
                    playerTitle:"void"
                  }
      this.feed.bind(this);
      this.pet.bind(this);
      this.talk.bind(this);
      this.hen.bind(this);
      this.typeWriter.bind(this);
    }



  action(callback){
    this.setState({playerTurn:!this.state.playerTurn});
    if(!this.state.speaking){
      this.setState({speaking:true})
      callback();
    }

  }
  endText(){
    let txt = ""
    txt+=`Hen is gone. You've offered Hen ${(this.state.feedCount/7)*100}% of yourself.`
    if(this.state.feedCount >= 6){
      txt+=" You and Hen are one."
    }
    else if(this.state.feedCount > 4 && this.state.feedCount < 6 ){
          txt+=" Some days you wake up and forget who and where you are. Some days you feel that your emotions are not your own."
    }
    else if(this.state.feedCount > 2 && this.state.feedCount <= 4){
          txt+=" Sometimes your body produces actions that you swear are not your own."
    }
    else if(this.state.feedCount <= 2 && this.state.feedCount != 0){
          txt +=" Sometimes you grab things you did not intend to. Sometimes if you are not paying attention your writing becomes incomprehensible. "
    }
 

    if(this.state.feedCount > 1){
      txt += " As time goes on, details of Hen recede to the tip of your tongue."
    }
    else{
      txt += " The next day, the idea of Hen becomes something abstract that you can't quite place."
    }


    if(this.state.petCount/7 > 0.5){
        txt += " Several years later, in a time of grief, you feel something wipe away your tears. You know things will be okay."
    }
    else if(this.state.petCount/7 <=0.5 && this.state.petCount/7 != 0){
        txt += " That night when falling asleep, you feel someone pull up the blankets and give you a kiss on the head."
    }
    else if(this.state.petCount==0){
        txt+=" You feel truly alone."
    }

    if(this.state.talkCount>=6){
        txt+=" Some days when waking from a dream you think that you've understood what Hen had to say. But the memories fade too fast."
    }
    else if(this.state.talkCount>1){
        txt+=" Though the memory of Hen has faded, you will never forget what Hen felt like."
    }
    else{
        txt+=" Nobody will understand your experience."
    }

    if(this.state.gameOver){
      this.props.stopMusic("god");

    }
    return txt;


  }

  feed(){
    console.log(Object.keys(story.feed).length+1)
    if(this.state.feedCount == Object.keys(story.feed).length+1){
      this.setState({gameOver:true, playerTitle:"The Giver"})
    }
    else{
      this.setState({text:""},this.typeWriter(story.feed[this.state.feedCount].text,0,e=>this.setState({henCount:this.state.henCount+1},this.hen()),upVoice));
    }
  }
  
  pet(){
    if(this.state.petCount == Object.keys(story.pet).length+1){
        this.setState({gameOver:true, playerTitle:"Big Cuddle Monster"})      
      }
    else{
        this.setState({text:""}, this.typeWriter(story.pet[this.state.petCount].text,0,e=>this.setState({henCount:this.state.henCount+1},this.hen()),upVoice));
      }
    }
  talk(){
    if(this.state.talkCount==Object.keys(story.talk).length+1){
        this.setState({gameOver:true, playerTitle:"The Conversationalist"})
    }
    else{
      this.setState({text:""}, this.typeWriter(story.talk[this.state.talkCount].text,0,e=>this.setState({henCount:this.state.henCount+1},this.hen()),upVoice));
    }
  }

  hen(){
      if(this.state.henCount == Object.keys(story.hen).length){
        this.setState({gameOver:true, playerTitle:"Caretaker"})
      }
      else{
        setTimeout(e=>this.setState({text:""}, this.typeWriter(story.hen[this.state.henCount].text,0,e=>this.setState({playerTurn:!this.state.playerTurn}))), 2000)
      }
      // TODO: set the timeout, have Hen reply... Then after 2 seconds, it's the players turn again.
  }

  typeWriter(txt,i,callback=0,voice=lowVoice,speed=80) {
    if(i==txt.length){
      this.setState({speaking:false})
      console.log("reached end and got: ",callback)
      if(callback){callback()}
      return
    }
    else{
      let phone =  new Audio(voice);
      phone.play();
      this.setState({text:this.state.text+txt.charAt(i)})
      setTimeout(()=>this.typeWriter(txt,i+1,callback,voice,speed),speed);

    }

  }

  render(){
    return(
  <div className="">
    <GlitchClip>
      <div className="godText">{this.state.text}</div>
    </GlitchClip>

    <div className="godControls">
      <div className={this.state.playerTurn ? "godUIbutton godUIcolorActive" : "godUIbutton godUIcolorDisabled"}
        onClick={this.state.playerTurn ? ()=>this.setState({feedCount:this.state.feedCount+1}, e=>this.action(e=>this.feed())) : e=>{}}>
        feed</div> 
      <div className={this.state.playerTurn ? "godUIbutton godUIcolorActive" : "godUIbutton godUIcolorDisabled"}
        onClick={this.state.playerTurn ? ()=>this.setState({petCount:this.state.petCount+1}, e=>this.action(e=>this.pet())) : e=>{}}>
        pet</div>
      <div className={this.state.playerTurn ? "godUIbutton godUIcolorActive" : "godUIbutton godUIcolorDisabled"}
        onClick={this.state.playerTurn ? ()=>this.setState({talkCount:this.state.talkCount+1}, e=>this.action(e=>this.talk())) : e=>{}}>
        talk</div>
    </div>

  <div className={this.state.gameOver? "gameOver" : "invisibile"}>
    Game End<p/>
    You've earned the title: <b>{this.state.playerTitle}</b><p/>
    {this.endText()}
    <hr/>
    You've fed Hen {this.state.feedCount + this.props.preTotals.totalFeed} times.<br/>
    You've pet Hen {this.state.petCount + this.props.preTotals.totalPet} times.<br/>
    You've spoken to Hen {this.state.talkCount + this.props.preTotals.totalTalk} times.<br/>
    <hr/>
    <p/>
    <i>Thankyou for playing!</i> :-)<br/>
    follow me @dvd2vec

  </div>
  </div>

      )

    }
}


export {GenericWindow, slideDown, OpenWindow, DropDown, OpenWindowForever, Icon, SimpleDropDown, OpenGallery, GodFinal};

